import React from 'react'
import { Link } from 'gatsby'
import { GoHome } from 'react-icons/go'

import { partPathFromNode, chapterPathFromNode } from '../helpers'
import { Breadcrumb, BreadcrumbItem } from 'reactstrap'

export default ({ node }) => {
  const isChapter = node.nChapter
  const isPart = node.nPart && !node.nChapter
  const isMatter = node.matter && !node.nPart
  const isClasse = node.classe && !node.matter
  const isExo = node.type === `exo`

  let partString
  let partPath
  if (isPart || isChapter) {
    partString = `Partie ${node.nPart}: ${node.part}`
    partPath = partPathFromNode(node)
  }

  let chapterString
  let chapterPath
  if (isChapter || isExo) {
    chapterString = `Chapitre ${node.nChapter}: ${node.chapter}`
    chapterPath = chapterPathFromNode(node)
  }

  return (
    <Breadcrumb>
      <BreadcrumbItem>
        <Link to="/">
          <GoHome />
        </Link>
      </BreadcrumbItem>

      <BreadcrumbItem active={isClasse}>
        {isClasse ? (
          node.classe
        ) : (
          <Link to={`/` + node.classe}>{node.classe}</Link>
        )}
      </BreadcrumbItem>

      {!isClasse && (
        <BreadcrumbItem active={isMatter}>
          {isMatter ? (
            node.matter
          ) : (
            <Link to={`/` + node.classe + `/` + node.matter}>
              {node.matter}
            </Link>
          )}
        </BreadcrumbItem>
      )}

      {!isClasse && !isMatter && (
        <BreadcrumbItem active={isPart}>
          {isPart ? partString : <Link to={partPath}>{partString}</Link>}
        </BreadcrumbItem>
      )}
      {(isChapter || isExo) && (
        <BreadcrumbItem active={!isExo}>
          {!isExo ? (
            chapterString
          ) : (
            <Link to={chapterPath}>{chapterString}</Link>
          )}
        </BreadcrumbItem>
      )}
      {(isChapter || isExo) && (
        <BreadcrumbItem active={isExo}>
          {isExo ? (
            `Exercices`
          ) : (
            <Link to={chapterPath + `/exo`}>Exercices</Link>
          )}
        </BreadcrumbItem>
      )}
    </Breadcrumb>
  )
}
